import { Subject } from 'rxjs/Subject';
import { SingletonService } from './../services/singleton.service';
import { Component, OnInit, Input, EventEmitter, Output, Inject } from '@angular/core';
import { APIv3Service } from '../services/api3.service';
import { NgForm } from '@angular/forms';
import { CalendarEvent } from 'angular-calendar';
import * as moment from 'moment';
import { DOCUMENT } from '@angular/platform-browser';
import { findLast } from '@angular/compiler/src/directive_resolver';

@Component({
    selector: 'app-subscription',
    templateUrl: './subscription.component.html',
    styleUrls: ['./subscription.component.css']
})
export class SubscriptionComponent implements OnInit {
    eventLock;
    bearerToken;

    @Input() calendarEvent: CalendarEvent;
    @Output() eventSelected = new EventEmitter<{ currentAction: number, event: Event }>();

    firstName: string;
    lastName: string;
    phone: string;
    email: string;
    description: string;
    courtesy: string;
    sagendaTranslationSubscriptionTitle: string;
    sagendaTranslationSubscriptionTitleMrs: string;
    sagendaTranslationSubscriptionTitleMr: string;
    sagendaTranslationSubscriptionTitleMiss: string;
    sagendaTranslationSubscriptionTitleDr: string;
    sagendaTranslationSubscriptionFirstName: string;
    sagendaTranslationSubscriptionLastName: string;
    sagendaTranslationSubscriptionEmail: string;
    sagendaTranslationSubscriptionPhone: string;
    sagendaTranslationSubscriptionDescription: string;
    sagendaDateFormat: string;
    sagendaTimeFormat: string;
    startFormatted: string;
    endFormatted: string;

    constructor(private apiv3Service: APIv3Service,
        private singletonService: SingletonService,
        @Inject(DOCUMENT) private document: any) {
        this.sagendaTranslationSubscriptionTitle = _window().sagendaTranslationSubscriptionTitle;
        this.sagendaTranslationSubscriptionTitleMrs = _window().sagendaTranslationSubscriptionTitleMrs;
        this.sagendaTranslationSubscriptionTitleMr = _window().sagendaTranslationSubscriptionTitleMr;
        this.sagendaTranslationSubscriptionTitleMiss = _window().sagendaTranslationSubscriptionTitleMiss;
        this.sagendaTranslationSubscriptionTitleDr = _window().sagendaTranslationSubscriptionTitleDr;
        this.sagendaTranslationSubscriptionFirstName = _window().sagendaTranslationSubscriptionFirstName;
        this.sagendaTranslationSubscriptionLastName = _window().sagendaTranslationSubscriptionLastName;
        this.sagendaTranslationSubscriptionEmail = _window().sagendaTranslationSubscriptionEmail;
        this.sagendaTranslationSubscriptionPhone = _window().sagendaTranslationSubscriptionPhone;
        this.sagendaTranslationSubscriptionDescription = _window().sagendaTranslationSubscriptionDescription;
        this.sagendaDateFormat = _window().sagendaDateFormat;
        this.sagendaTimeFormat = _window().sagendaTimeFormat;

        this.bearerToken = this.singletonService.bearerToken;

        this.singletonService.bearerToken.subscribe(
            (bearerToken: string) => { this.bearerToken = bearerToken; }
        );
    }

    ngOnInit() {
        this.startFormatted = moment(this.calendarEvent.start).utc().format(this.sagendaDateFormat + ' ' + this.sagendaTimeFormat);
        this.endFormatted = moment(this.calendarEvent.end).utc().format(this.sagendaDateFormat + ' ' + this.sagendaTimeFormat);
        this.apiv3Service.getEventLock(this.bearerToken, this.calendarEvent.meta.eventIdentifier).subscribe(
            object => {
                this.eventLock = object.identifier;
            }
        );
    }

    onSubmit(form: NgForm) {
        console.log('Submitted !' + form);
        this.calendarEvent.meta.courtesy = form.value.courtesy;
        this.calendarEvent.meta.firstName = form.value.firstName;
        this.calendarEvent.meta.lastName = form.value.lastName;
        this.calendarEvent.meta.phone = form.value.phone;
        this.calendarEvent.meta.email = form.value.email;
        this.calendarEvent.meta.description = form.value.description;

        this.setBooking();
    }

    /**
     * Set Booking
     */
    setBooking(): void {
        this.calendarEvent.meta.eventLock = this.eventLock;
        try {
            this.apiv3Service.setBooking(this.calendarEvent, this.bearerToken).subscribe(
                result => {
                    if (result.paymentUrl !== null) {
                        this.document.location.href = result.paymentUrl;

                    } else {
                        // todo : use internal root?
                        this.eventSelected.emit({ currentAction: 3, event: result.event });
                    }
                }
            );
        } catch (error) {
            // TODO : log error
            window.location.href = error.paymentURL;
        }
        finally {

        }

    }
}

/**
 * Return the window native object
 */
function _window(): any {
    return window;
}



