import { ProgressBarService } from './../services/progress-bar.service';
import { Component, OnInit } from '@angular/core';
import { BehaviorSubject } from 'rxjs/BehaviorSubject';

@Component({
    selector: 'app-progress-bar',
    templateUrl: './progress-bar.component.html'
})

export class ProgressBarComponent implements OnInit {
    public progressBarStatus;

    constructor(private progressBarService: ProgressBarService) {
        this.progressBarStatus = progressBarService.progressBarStatus;
    }

    displayProgressBar(value: boolean) {
        this.progressBarService.displayProgressBar(value);
    }

    ngOnInit() {
        this.progressBarService.progressBarStatus.subscribe((val: boolean) => {
            this.progressBarStatus = val;
        });
    }
}



