import { SingletonService } from './services/singleton.service';
import { PreviousTodayNextComponent } from 'app/previous-today-next-buttons/previous-today-next-buttons.component';
import { ProgressBarService } from './services/progress-bar.service';
import { ProgressBarComponent } from './progress-bar/progress-bar.component';
import { TimeleftPipe } from './timeleft.pipe';
import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { FormsModule } from '@angular/forms';
import { HttpClientModule } from '@angular/common/http';
import { CommonModule } from '@angular/common';

import { AppComponent } from './app.component';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';
import { CalendarModule } from 'angular-calendar';
import { APIv3Service } from './services/api3.service';
import { SubscriptionComponent } from './subscription/subscription.component';
import { CalendarComponent } from './calendar/calendar.component';
import { ListComponent } from './list/list.component';

import { BsDropdownModule } from 'ngx-bootstrap';

import { registerLocaleData } from '@angular/common';
import localeFr from '@angular/common/locales/fr';
registerLocaleData(localeFr, 'fr');


@NgModule({
    declarations: [
        AppComponent,
        SubscriptionComponent,
        CalendarComponent,
        ListComponent,
        ProgressBarComponent,
        TimeleftPipe,
        PreviousTodayNextComponent
    ],
    imports: [
        BsDropdownModule.forRoot(),
        BrowserModule,
        CommonModule,
        FormsModule,
        HttpClientModule,
        BrowserAnimationsModule,
        CalendarModule.forRoot()
    ],
    providers: [APIv3Service, ProgressBarService, ProgressBarComponent, PreviousTodayNextComponent, SingletonService],
    bootstrap: [AppComponent]
})
export class AppModule {
}



