import { BehaviorSubject } from 'rxjs/BehaviorSubject';
import { Subject } from 'rxjs/Subject';
import { Injectable } from '@angular/core';

@Injectable()
export class SingletonService {
    private _viewDate = new BehaviorSubject<Date>(null);
    public viewDate = this._viewDate.asObservable();

    private _bearerToken = new BehaviorSubject<string>(null);
    public bearerToken = this._bearerToken.asObservable();

    constructor() {
        this._viewDate.next(new Date);
        this._bearerToken.next('');
    }

    setViewDate(object) {
        this._viewDate.next(object);
    }

    setBearerToken(object) {
        this._bearerToken.next(object);
    }
}



