import { Pipe, PipeTransform } from '@angular/core';

@Pipe({
    name: 'timeleft'
})
export class TimeleftPipe implements PipeTransform {
    transform(value: any) {
        const dateNow = new Date();
        const milliseconds: number = (Math.ceil(value.getTime()) - Math.ceil(dateNow.getTime() + dateNow.getTimezoneOffset() * 60 * 1000 ));
        return this.dhms(milliseconds);
    }

    dhms(t) {
        let days, hours, minutes;
        days = Math.floor(t / 8.64e+7);
        t -= days * 8.64e+7;
        hours = Math.floor(t / 3.6e+6);
        t -= hours * 3.6e+6;
        minutes = Math.floor(t / 60000);

        return [
            days + 'day(s)',
            hours + 'hours',
            minutes + 'min'
        ].join(' ');
    }
}



